"use client";

import { useState, useRef } from "react";
import Image from "next/image";
import Badge from "@/src/components/base/Badge";
import { Download, Loader2 } from "lucide-react";

type DownloadCardProps = {
  title: string;
  description?: string;
  fileType?: "PDF" | "ZIP" | "DOC" | "PPT";
  fileSize?: string;
  driveFileId: string;
  thumbnail?: string;
};

const DownloadCard = ({
  title,
  description,
  fileType = "PDF",
  fileSize,
  driveFileId,
  thumbnail,
}: DownloadCardProps) => {
  const downloadUrl = `https://drive.google.com/uc?export=download&id=${driveFileId}`;

  const [isDownloading, setIsDownloading] = useState(false);
  const [elapsed, setElapsed] = useState(0);
  const timerRef = useRef<NodeJS.Timeout | null>(null);
  const startTimeRef = useRef<number>(0);

  const handleDownload = () => {
    if (isDownloading) return;

    setIsDownloading(true);
    setElapsed(0);
    startTimeRef.current = performance.now();

    // Start timer
    timerRef.current = setInterval(() => {
      const seconds =
        (performance.now() - startTimeRef.current) / 1000;
      setElapsed(seconds);
    }, 100);

    // Trigger browser download
    const link = document.createElement("a");
    link.href = downloadUrl;
    link.download = "";
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);

    // Stop timer after short delay (browser takes over)
    setTimeout(() => {
      if (timerRef.current) clearInterval(timerRef.current);
      setIsDownloading(false);
    }, 3000);
  };

  return (
    <div className="rounded-2xl border border-black/10 bg-white shadow-md hover:shadow-xl transition p-4 flex flex-col justify-between">
      
      {/* Thumbnail */}
      {thumbnail && (
        <div className="relative h-36 w-full mb-4 rounded-xl overflow-hidden">
          <Image
            src={thumbnail}
            alt={title}
            fill
            className="object-cover"
          />
        </div>
      )}

      {/* Content */}
      <div className="flex flex-col gap-2 flex-1">
        <div className="flex items-center justify-between">
          <h3 className="font-semibold text-gray-800 text-base">
            {title}
          </h3>
          <Badge>{fileType}</Badge>
        </div>

        {description && (
          <p className="text-sm text-gray-600 line-clamp-2">
            {description}
          </p>
        )}

        {fileSize && (
          <p className="text-xs text-gray-500">
            File size: {fileSize}
          </p>
        )}
      </div>

      {/* Download Button */}
      <button
        onClick={handleDownload}
        disabled={isDownloading}
        className="mt-4 inline-flex items-center justify-center gap-2 rounded-xl bg-black text-white py-2 text-sm font-medium hover:bg-black/90 transition disabled:opacity-70"
      >
        {isDownloading ? (
          <>
            <Loader2 className="animate-spin" size={16} />
            Downloading {elapsed.toFixed(1)}s
          </>
        ) : (
          <>
            <Download size={16} />
            Download
          </>
        )}
      </button>
    </div>
  );
};

export default DownloadCard;
